          this.jyls.push(index)
        })
    },
    readExcel (e) {
      const that = this
      const files = e.target.files
      if (files.length < 1) {
        return false
      } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {
        this.$toast('xlsxlsx')
        return false
      }

      const fileReader = new FileReader()
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result
          const workbook = XLSX.read(data, {
            type: 'binary'
          }) // 
          const wsname = workbook.SheetNames[0] // 
          console.log(wsname)
          const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]) // json
          // const ws1 = XLSX.utils.sheet_to_slk(workbook.Sheets[wsname]) // 
          // const ws2 = XLSX.utils.sheet_to_html(workbook.Sheets[wsname]) // HTML
          // const ws3 = XLSX.utils.sheet_to_csv(workbook.Sheets[wsname]) // 
          // const ws4 = XLSX.utils.sheet_to_formulae(workbook.Sheets[wsname]) // 
          // const ws5 = XLSX.utils.sheet_to_txt(workbook.Sheets[wsname]) // UTF16

          that.outputs = [] // 

          for (let i = 0; i < ws.length; i++) {
            const sheetData = ws[i] // 
            that.outputs.push(sheetData)
            console.log(sheetData)
          }

          this.$refs.upload.value = ''
        } catch (e) {
          console.log(e)
          return false
        }
      }
      fileReader.readAsBinaryString(files[0])
    }
  },
